import React from "react"

const ContactSection = React.forwardRef((props, ref) => (
  <div class="split style1" ref={ref}>
    <section>
      <form method="post" action="#">
        <div class="fields">
          <div class="field half">
            <label for="name">Name</label>
            <input type="text" name="name" id="name" />
          </div>
          <div class="field half">
            <label for="email">Email</label>
            <input type="text" name="email" id="email" />
          </div>
          <div class="field">
            <label for="message">Message</label>
            <textarea name="message" id="message" rows="5"></textarea>
          </div>
        </div>
        <ul class="actionsContact">
          <li>
            <a href="" class="button submit">
              Send Message
            </a>
          </li>
          <li>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="../../ChristopherMaselli_Resume.jpg"
              class="button submit"
            >
              View Resume
            </a>
          </li>
        </ul>
      </form>
    </section>
    <section>
      <ul class="contact">
        <li>
          <h3>Address</h3>
          <span>
            455 East 14th Street
            <br />
            New York, NY 10009
            <br />
            USA
          </span>
        </li>
        <li>
          <h3>Email</h3>
          <a href="#">Christopherfmaselli@gmail.com</a>
        </li>
        <li>
          <h3>Phone</h3>
          <span>(201) 961-4859</span>
        </li>
        <li>
          <h3>Github</h3>
          <a href="https://github.com/ChristopherMaselli">
            Github.com/ChristopherMaselli
          </a>
        </li>
        <li>
          <h3>LinkedIn</h3>
          <a href="https://www.linkedin.com/in/christopher-maselli-b51864ab/">
            Linkedin.com/in/christopher-maselli-b51864ab
          </a>
        </li>
      </ul>
    </section>
  </div>
))

export default ContactSection
